import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PreDefinedDishComponent } from './pre-defined-dish.component';

const routes: Routes = [
  {
    path: '',
    component: PreDefinedDishComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PreDefinedDishRoutingModule {}
